.profile{
    text-align: center;
    max-width: 410px;
    margin: 0 auto;
    border: 3px solid #dee2e6;
    border-radius: 13px;
}

.description{
    background-color: white;
}

.avatar {
    height: 100%;
    height: 150px;
    margin-top: 50px;
    border-radius: 50%;
    border: 3px solid #dee2e670;
}

.name {
    font-weight: bold;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.1;
    margin-bottom: 0;
}

.tag {
    margin-bottom: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    text-align: center;

    color: #999999;
}

.location {
    margin-top: 5px;
    padding-bottom: 40px;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    text-align: center;

    color: #999999;
}

.stats li{
    display: flex;
    flex-direction: column;
    list-style: none;
    
}
.stats {
    display: flex;
    padding: 0;
    justify-content: space-evenly;
}

.label {
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    text-align: center;

    color: #999999;
}

.quantity {
    font-size: 14px;
    line-height: 25px;
    font-weight: bold;
}