.statistics {
    text-align: center;
    max-width: 380px;
    margin: 0 auto;
    margin-top: 20px;

    background-color: #fff;
}

.title {
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 0;

    font-weight: bold;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.4;

    color: #535353;
}

.stat {
    padding: 0;
    margin: 0;
    display: flex;
    padding: 0;
    justify-content: space-evenly;
    
}

.stat li {
    padding: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    list-style: none;
}